import React, { useEffect } from "react";
import style from "./PDFComponent.module.css";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
import autoTable from "jspdf-autotable";

const PDFComponent = ({ result, setPdf, caseName, isOneQuestionOnCase }) => {
  useEffect(() => {
    const downloadPDF = () => {
      const capture = document.getElementById("pdf");
      html2canvas(capture).then((canvas) => {
        const imgData = canvas.toDataURL("img/png");
        const doc = new jsPDF("p", "mm", "a4");
        const componentWidth = doc.internal.pageSize.getWidth();
        const componentHeight = doc.internal.pageSize.getHeight();
        doc.addImage(imgData, "PNG", 0, 0, componentWidth, componentHeight);
        doc.save(`${caseName}.pdf`);
        setPdf(false);
      });
    };
    downloadPDF();
  }, []);
  return (
    <div id="pdf" className={style.main}>
      <h1
        style={{
          textAlign: "center",
          fontWeight: "100",
          padding: "5px 20px",
          backgroundColor: "rgba(0, 0, 0, 0.9)",
          borderRadius: "10px",
        }}
      >
        {!isOneQuestionOnCase
          ? `Результаты опроса: ${caseName}`
          : "Результаты опроса"}
      </h1>
      <section className={style.mainPDFContainer}>
        {result?.map((el, index) => {
          return (
            <div key={index}>
              {Object.entries(el).map((li, num) => {
                const colors = ["red", "blue", "green", "grey", "yellow"];
                return (
                  <div className={style.children} key={num}>
                    <h3
                      className={style.pdfParamName}
                      style={
                        li[0] === "вопросы"
                          ? {
                              backgroundColor: "brown",
                              padding: "5px 10px",
                              borderRadius: "10px",
                            }
                          : {}
                      }
                    >
                      {li[0] === "вопросы" ? "Вопрос:" : li[0] + " -"}

                      <span
                        style={li[0] === "вопросы" ? { color: "white" } : {}}
                        className={style.pdfParamValue}
                      >
                        {" " + li[1]}
                      </span>
                    </h3>
                  </div>
                );
              })}
            </div>
          );
        })}
      </section>
    </div>
  );
};

export default PDFComponent;
