import React, { useEffect, useState } from "react";
import style from "./CasesPanel.module.css";
import Excel from "../Excel/Excel";
import { getAllCases, getCurrentResult } from "../../DAL/dal";
import PDFComponent from "../Excel/PDFComponent/PDFComponent";
import { useSelector } from "react-redux";

const CasesPanel = ({ socket, reRender }) => {
  const [cases, setCases] = useState([]);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [currentCaseName, setCurrentCaseName] = useState("");
  const questions = useSelector((state) => state.questions);
  const [dataExport, setDataExport] = useState([]);
  const [result, setResult] = useState([]);
  const [pdf, setPdf] = useState(false);
  const [isOneQuestionOnCase, setIsOneQuestionOnCase] = useState(false);

  useEffect(() => {
    getAllCases().then((r) => {
      setCurrentIndex(r.data.index);
      setCases(r.data.cases);
    });
  }, []);

  useEffect(() => {
    if (cases.length) {
      const arrayValidationLength = [];
      for (let i = 0; i < cases.length; i++) {
        if (cases[i].questions.length === 1) {
          arrayValidationLength.push("/");
        }
      }
      if (arrayValidationLength.length === cases.length) {
        setIsOneQuestionOnCase(true);
      }
    }
  }, [cases]);

  const setGlobalCaseIndex = (index) => {
    socket.emit("setCase", index);
    setCurrentIndex(index);
  };

  useEffect(() => {
    if (cases.length) {
      setCurrentCaseName(cases[currentIndex].caseName);
    }
  }, [cases, currentIndex]);

  useEffect(() => {
    getCurrentResult().then((r) => {
      setDataExport(r.data);
    });
  }, [reRender]);

  useEffect(() => {
    const array = [];
    if (isOneQuestionOnCase) {
      cases.map((el) => array.push({ name: el.caseName, list: [] }));
    } else {
      questions.questions.map((el) =>
        array.push({ name: el.blockName, list: [] })
      );
    }
    for (let i = 0; i < array.length; i++) {
      for (let j = 0; j < dataExport.length; j++) {
        if (dataExport[j].list[i]) {
          array[i].list.push(dataExport[j].list[i].name);
          // добавляем ответы относящиеся к конкретному вопросу
        }
      }
    }
    const newArray = [];
    for (let i = 0; i < array.length; i++) {
      // фильтруем варианты в новый массив с колличеством подсчитанных ответов
      newArray.push({
        вопросы: array[i].name,
        ...array[i].list.reduce((acc, i) => {
          if (acc.hasOwnProperty(i)) {
            acc[i] += 1;
          } else {
            acc[i] = 1;
          }
          return acc;
        }, {}),
      });
    }
    // console.log(newArray);
    setResult(newArray);
  }, [dataExport, currentCaseName]);

  return (
    <>
      {!pdf ? (
        <section
          style={{ display: "flex", flexDirection: "column", gap: "20px" }}
        >
          {cases.map((el, index) => (
            <div
              style={{
                background: `${currentIndex === index ? "white" : "black"} `,
                color: `${currentIndex === index ? "black" : "white"} `,
                borderRadius: "10px",
                padding: "10px 30px",
                cursor: "pointer",
                transform: `${
                  currentIndex === index ? "scale(1.3)" : "scale(1)"
                }`,
                transition: ".8s",
              }}
              onClick={() => {
                setGlobalCaseIndex(index);
              }}
              key={index}
            >
              {el.caseName}
            </div>
          ))}
          <Excel
            result={result}
            caseName={currentCaseName}
            pdf={pdf}
            setPdf={setPdf}
          />
        </section>
      ) : (
        <PDFComponent
          isOneQuestionOnCase={isOneQuestionOnCase}
          caseName={currentCaseName}
          setPdf={setPdf}
          result={result}
        />
      )}
    </>
  );
};

export default CasesPanel;
